import './MonthChunk.scss';

import { format } from 'date-fns';
import React from 'react';

export interface PropsMonthChunk {
  date: number;
  appsCount: number;
  solid?: boolean;
  setCurrentDate: (axis: number) => void;
}

const MonthChunk = ({
  date,
  appsCount,
  solid,
  setCurrentDate
}: PropsMonthChunk) => {
  const onClick = () => setCurrentDate(date);
  return (
    <span styleName={`monthChunk ${solid ? "solid" : ""}`}>
      <div styleName="weekday">{format(date, "EEEEEE")}</div>
      <div styleName="monthday" onClick={onClick}>
        {format(date, "d")}
      </div>
      <div styleName="appsCount">{appsCount}</div>
    </span>
  );
};

export default React.memo(MonthChunk);
