import './TimeLine.scss';

import { format, roundToNearestMinutes } from 'date-fns';
import { addMilliseconds } from 'date-fns/esm';
import React, { useMemo } from 'react';

import CenteredVertically from '../../../../../style-components/CenteredVertically';
import { ColumnsInfo, GridInfo, ScrollInfo, TimeLineInfo } from '../../../Calendar.containers';

const TimeLine = () => {
  const { timeLineHeight } = TimeLineInfo.useContainer();
  const { columnsCount, columnWidth } = ColumnsInfo.useContainer();
  const {
    gridStepDuration,
    oneDayStartTime,
    oneDayStepsCount
  } = GridInfo.useContainer();
  const {
    scrollOffsetDone,
    columnsPerScroll,
    oneScrollSizePixels
  } = ScrollInfo.useContainer();

  const columnsCountBuffered = columnsCount + columnsPerScroll * 2;
  return useMemo(
    () => (
      <div
        styleName="timeLine"
        style={{
          transform: `translateX(${(-scrollOffsetDone - 1) *
            oneScrollSizePixels}px)`,
          height: timeLineHeight
        }}
      >
        {new Array(columnsCountBuffered).fill(null).map((_, i) => {
          const iReal = i - columnsPerScroll;
          const scrolledColumns = -scrollOffsetDone * columnsPerScroll;
          const currentColumn = scrolledColumns + iReal;
          const columnAtDay =
            (currentColumn % oneDayStepsCount) +
            (currentColumn < 0 ? oneDayStepsCount : 0);

          const columnAtDayNormalized =
            columnAtDay >= oneDayStepsCount
              ? columnAtDay - oneDayStepsCount
              : columnAtDay;

          const stampDayOffset =
            columnAtDayNormalized * gridStepDuration.valueOf();
          const timestamp = addMilliseconds(oneDayStartTime, stampDayOffset);

          // console.log({
          //   i,
          //   iReal,
          //   scrolledColumns,
          //   currentColumn,
          //   columnAtDay,
          //   columnAtDayNormalized,
          //   timestamp: format(timestamp, "H:mm")
          // });

          return (
            <span
              key={i}
              styleName="timeMark"
              style={{
                width: columnWidth,
                background: !columnAtDayNormalized ? "lightgray" : "white"
              }}
            >
              <CenteredVertically>
                {format(roundToNearestMinutes(timestamp), "H:mm")}
              </CenteredVertically>
            </span>
          );
        })}
      </div>
    ),
    [
      scrollOffsetDone,
      columnsCountBuffered,
      gridStepDuration,
      timeLineHeight,
      columnWidth,
      oneScrollSizePixels,
      columnsPerScroll,
      oneDayStartTime,
      oneDayStepsCount
    ]
  );
};

export default TimeLine;
