import './ScrollingLayer.scss';

import classnames from 'classnames';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useDebounce, useWindowSize } from 'use-hooks';

import { StickyController } from '../../../../assembly/stickyController';
import { ColumnsInfo, ScrollInfo } from '../../Calendar.containers';
import AppointmentsPlacer from './AppointmentsPlacer';
import BackGrid from './BackGrid';
import RestrictedZones from './RestrictedZones';
import TimeLine from './TimeLine';

// interface Props {
//   // scrollDelta: number;
// }

interface SelfData {
  offsetWidth: number;
  offsetHeight: number;
}

const ScrollingLayer = () => {
  const {
    scrollOffset,
    scrollDuration,
    oneScrollSize,
    endScroll,
    setOneScrollSizePixels,
    oneScrollSizePixels,
    setOneScrollSizeReal,
    scrollIsJustReset
  } = ScrollInfo.useContainer();
  const { columnWidth } = ColumnsInfo.useContainer();

  const { width } = useWindowSize();
  const widthD = useDebounce(width, 500);

  const [selfData, setSelfData] = useState<SelfData>({
    offsetHeight: 0,
    offsetWidth: 0
  });
  const self = useRef<HTMLDivElement>(null);

  // useWhyDidYouUpdate("ScrollingLayer", {
  //   scrollOffset,
  //   scrollDuration,
  //   oneScrollSize,
  //   endScroll,
  //   setOneScrollSizePixels,
  //   oneScrollSizePixels,
  //   setOneScrollSizeReal,
  //   columnWidth,
  //   width,
  //   widthD
  // });

  // self data collecting hook
  useEffect(() => {
    if (self.current) {
      const { offsetWidth, offsetHeight } = self.current;
      setSelfData({ offsetWidth, offsetHeight });
    }
  }, [widthD]);

  // initization hook
  useEffect(() => {
    // it means that selfData is still not-initialized
    if (!selfData.offsetWidth) return;

    const pixelsPerScroll =
      Math.floor((selfData.offsetWidth * oneScrollSize) / columnWidth) *
      columnWidth;

    setOneScrollSizePixels(pixelsPerScroll);
    setOneScrollSizeReal(pixelsPerScroll / selfData.offsetWidth);
  }, [
    selfData,
    columnWidth,
    oneScrollSize,
    setOneScrollSizeReal,
    setOneScrollSizePixels
  ]);

  const translateX = scrollOffset * oneScrollSizePixels;
  const transform = `translate3d(${translateX}px, ${0}px, ${0}px)`;

  const onTransitionEnd = useCallback(() => setTimeout(() => endScroll()), [
    endScroll
  ]);

  const topBlockContent = useMemo(() => <TimeLine />, []);
  const bottomBlockContent = useMemo(
    () => (
      <>
        <BackGrid />
        <AppointmentsPlacer />
        <RestrictedZones />
      </>
    ),
    []
  );

  const topBlockRef = useRef<HTMLDivElement>(null);

  // sticky hook
  useEffect(() => {
    const element = topBlockRef.current as HTMLDivElement;
    StickyController.addStickyElement(element, 1);
  }, []);

  const content = useMemo(
    () => (
      <>
        <div
          ref={topBlockRef}
          styleName="topBlock scrollable"
          style={{ transform, transitionDuration: `${scrollDuration}s` }}
          onTransitionEnd={onTransitionEnd}
        >
          {topBlockContent}
        </div>
        <div
          styleName="bottomBlock scrollable"
          style={{ transform, transitionDuration: `${scrollDuration}s` }}
        >
          {bottomBlockContent}
        </div>
      </>
    ),
    [
      transform,
      scrollDuration,
      onTransitionEnd,
      topBlockContent,
      bottomBlockContent
    ]
  );

  return useMemo(
    () => (
      <div
        styleName={classnames(
          "scrollingLayer",
          scrollIsJustReset ? "scrollIsJustReset" : ""
        )}
        ref={self}
      >
        {content}
      </div>
    ),
    [self, content, scrollIsJustReset]
  );
};

export default ScrollingLayer;
