import { List } from 'immutable';
import { useCallback, useState } from 'react';
import { createContainer } from 'unstated-next';
import { v4 } from 'uuid';

import { areIntervalsIntersects, IntervalInt, withinInterval } from '../assembly/interval';
import { RowsInfo } from '../components/Calendar/Calendar.containers';

function maxInterval(i1: IntervalInt, i2: IntervalInt): IntervalInt {
  return {
    end: Math.max(i1.end, i2.end),
    start: Math.min(i1.start, i2.start)
  };
}

export interface AppointmentUnassigned {
  id: string;
  userId: string;
  interval: IntervalInt;
  rowIndex: number;
  blockId?: string;
}

export type Appointment = Required<AppointmentUnassigned>;

export interface AppointmentsBlock {
  id: string;
  interval: IntervalInt;
  size: number;
  elements: List<Appointment>;
}

export function createAppointment(
  args: Partial<AppointmentUnassigned> & Omit<AppointmentUnassigned, "id">
): AppointmentUnassigned {
  return {
    ...args,
    id: args.id || v4()
  };
}

export interface ShiftSolution {
  up: string[];
  down: string[];
}

export enum Result {
  Success,
  Fail
}

function useAppointments(initialStateOrigin = { blockSize: 25 }) {
  const [initialState] = useState(initialStateOrigin);
  const { blockSize } = initialState;
  const { rowsCount } = RowsInfo.useContainer();

  const [appointmentBlocks, setAppointmentBlocks] = useState<
    List<AppointmentsBlock>
  >(List.of<AppointmentsBlock>());

  const addAppointment = useCallback(
    (app: Appointment) => {
      const blockIndex = appointmentBlocks.findLastIndex(
        block =>
          block.elements.size <= blockSize &&
          (block.interval.start.valueOf() <= app.interval.start.valueOf() ||
            block.interval.start === Infinity)
      );

      const targetBlock = appointmentBlocks.get(
        blockIndex
      ) as AppointmentsBlock;
      const appWithBlockId: Appointment = { ...app, blockId: targetBlock.id };
      const newApps =
        blockIndex === -1
          ? appointmentBlocks.push({
              elements: List.of(appWithBlockId),
              id: v4(),
              interval: app.interval,
              size: 1
            })
          : appointmentBlocks.set(blockIndex, {
              elements: targetBlock.elements
                .push(appWithBlockId)
                .sortBy(a => a.interval.start),
              id: targetBlock.id,
              interval: maxInterval(targetBlock.interval, app.interval),
              size: targetBlock.size + 1
            });

      setAppointmentBlocks(newApps);
    },
    [appointmentBlocks, blockSize]
  );

  const addAppointments = useCallback(
    (apps: AppointmentUnassigned[]) => {
      const blockIndex2apps: Record<number, Appointment[]> = apps.reduce(
        (acc: Record<number, Appointment[]>, app) => {
          const blockIndex = appointmentBlocks.findLastIndex(
            (block, i) =>
              block.elements.size + (acc[i] || { length: 0 }).length <
                blockSize &&
              block.interval.start.valueOf() <= app.interval.start.valueOf()
          );

          const block = appointmentBlocks.get(blockIndex);

          const appWithIndex: Appointment = {
            ...app,
            blockId: block ? block.id : "none"
          };
          return {
            ...acc,
            [blockIndex]: [...(acc[blockIndex] || []), appWithIndex]
          };
        },
        {}
      );

      // TODO: mark each Appointment with block id
      const appsWithoutBlock = blockIndex2apps[-1] || [];
      const newBlocks = Array.from(
        List(appsWithoutBlock)
          .sortBy(a => a.interval.start)
          .reduce((acc: AppointmentsBlock[], val: AppointmentUnassigned) => {
            if (!acc.length) {
              const blockId = v4();
              const block: AppointmentsBlock = {
                elements: List.of({ ...val, blockId }),
                id: blockId,
                interval: val.interval,
                size: 1
              };

              return [block];
            }

            const currentBlockIndex =
              acc[acc.length - 1].size < blockSize
                ? acc.length - 1
                : acc.length;
            const currentBlock: AppointmentsBlock = acc[currentBlockIndex] || {
              elements: List(),
              id: v4(),
              interval: {
                end: -Infinity,
                start: Infinity
              },
              size: 0
            };

            const appWithBlockId: Appointment = {
              ...val,
              blockId: currentBlock.id
            };

            return [
              ...acc.slice(0, currentBlockIndex),
              {
                elements: currentBlock.elements.push(appWithBlockId),
                id: currentBlock.id,
                interval: maxInterval(currentBlock.interval, val.interval),
                size: currentBlock.size + 1
              }
            ];
          }, [])
      );

      const updatedAppBlocks: List<AppointmentsBlock> = appointmentBlocks.map(
        (block, i) =>
          i in blockIndex2apps
            ? {
                ...block,
                elements: block.elements
                  .concat(blockIndex2apps[i])
                  .sortBy(({ interval }) => interval.start),
                id: block.id,
                size: block.size + blockIndex2apps[i].length,
                interval: blockIndex2apps[i].reduce(
                  (acc: IntervalInt, val: Appointment) =>
                    maxInterval(acc, val.interval),
                  block.interval
                )
              }
            : block
      );

      setAppointmentBlocks(
        updatedAppBlocks.concat(newBlocks).sortBy(b => b.interval.start)
      );
    },
    [appointmentBlocks, blockSize]
  );

  const getAppCoords = useCallback(
    (
      id: string,
      appointmentBlocksCurrent: List<AppointmentsBlock> = appointmentBlocks
    ) => {
      const coords: {
        blockId: number;
        appIndex: number;
      } | null = appointmentBlocksCurrent.reduce(
        (
          coords: { blockId: number; appIndex: number } | null,
          block,
          blockId
        ) => {
          if (coords) return coords;

          const appIndex = block.elements.findIndex(app => app.id === id);
          if (appIndex === -1) return coords;

          return { blockId, appIndex };
        },
        null
      );

      if (coords === null) throw new Error("Cannot find the app!");

      return coords;
    },
    [appointmentBlocks]
  );

  const getAppById = useCallback(
    (id: string) => {
      const { appIndex, blockId } = getAppCoords(id);
      const app = (appointmentBlocks.get(
        blockId
      ) as AppointmentsBlock).elements.get(appIndex);

      return app;
    },
    [getAppCoords, appointmentBlocks]
  );

  const shiftAppForBlocks = useCallback(
    (
      shiftingAppId: string,
      delta: number,
      appointmentBlocks: List<AppointmentsBlock>
    ) => {
      const { blockId, appIndex } = getAppCoords(
        shiftingAppId,
        appointmentBlocks
      );
      const block: AppointmentsBlock = appointmentBlocks.get(
        blockId
      ) as AppointmentsBlock;
      const updatedBlock: AppointmentsBlock = {
        ...block,
        elements: block.elements.update(appIndex, previousApp => ({
          ...previousApp,
          rowIndex: previousApp.rowIndex + delta
        }))
      };

      const newAppointmentBlocks = appointmentBlocks.update(
        blockId,
        () => updatedBlock
      );

      return newAppointmentBlocks;
    },
    [getAppCoords]
  );

  // TODO: not to override existing block - move newApp to another block instead
  const updateAppForBlocks = useCallback(
    (
      newApp: Pick<Appointment, "id"> & Partial<Appointment>,
      appointmentBlocks: List<AppointmentsBlock>
    ) => {
      const { blockId, appIndex } = getAppCoords(newApp.id, appointmentBlocks);
      const block: AppointmentsBlock = appointmentBlocks.get(
        blockId
      ) as AppointmentsBlock;
      const updatedBlock: AppointmentsBlock = {
        ...block,
        interval: maxInterval(block.interval, newApp.interval as IntervalInt),
        elements: block.elements.update(appIndex, previousApp => ({
          ...previousApp,
          ...newApp
        }))
      };

      const newAppointmentBlocks = appointmentBlocks.update(
        blockId,
        () => updatedBlock
      );

      return newAppointmentBlocks;
    },
    [getAppCoords]
  );

  const updateApp = useCallback(
    (newApp: Appointment) => {
      const newAppointmentBlocks = updateAppForBlocks(
        newApp,
        appointmentBlocks
      );
      setAppointmentBlocks(newAppointmentBlocks);
    },
    [appointmentBlocks, updateAppForBlocks]
  );

  const updateApps = useCallback(
    (newApps: Appointment[]) => {
      const newAppointmentBlocks = newApps.reduce(
        (acc, val) => updateAppForBlocks(val, acc),
        appointmentBlocks
      );
      setAppointmentBlocks(newAppointmentBlocks);
    },
    [appointmentBlocks, updateAppForBlocks]
  );

  const getApp = useCallback(
    (date: Date | number, row: number) => {
      const blocks = appointmentBlocks.filter(block =>
        withinInterval(date, block.interval)
      );
      const app = blocks.reduce(
        (app: Appointment | null, block) =>
          app
            ? app
            : block.elements.find(
                app =>
                  app.rowIndex === row &&
                  withinInterval(date, app.interval, true, false)
              ) || null,
        null
      );

      return app;
    },
    [appointmentBlocks]
  );

  const getVerticalNeighboursByApp: (
    targetApp: Appointment,
    appointmentBlocksCurrent?: List<AppointmentsBlock>
  ) => List<Appointment> = useCallback(
    (
      targetApp: Appointment,
      appointmentBlocksCurrent = appointmentBlocks
    ): List<Appointment> => {
      const areVerticalNeighbours = (
        app1: Appointment,
        app2: Appointment
      ): boolean =>
        Math.abs(app1.rowIndex - app2.rowIndex) === 1 &&
        areIntervalsIntersects(app1.interval, app2.interval, false, false);

      // we are looking only to 1 level of blocks recursion
      const intersectingBlocks = appointmentBlocksCurrent.filter(block =>
        areIntervalsIntersects(block.interval, targetApp.interval)
      );
      const totalNeighbours = intersectingBlocks.reduce(
        (acc: List<Appointment>, block: AppointmentsBlock) =>
          acc.concat(
            block.elements.filter(app => areVerticalNeighbours(targetApp, app))
          ),
        List.of()
      );
      return totalNeighbours;
    },
    [appointmentBlocks]
  );

  const getVerticalNeighboursById = useCallback(
    (
      appId: string,
      appointmentBlocksCurrent = appointmentBlocks
    ): List<Appointment> => {
      const { blockId, appIndex } = getAppCoords(
        appId,
        appointmentBlocksCurrent
      );
      const originBlock = appointmentBlocksCurrent.get(blockId);
      const targetApp = originBlock.elements.get(appIndex);

      return getVerticalNeighboursByApp(targetApp, appointmentBlocksCurrent);
    },
    [appointmentBlocks, getAppCoords, getVerticalNeighboursByApp]
  );

  const getIntersectingApps = useCallback(
    (row: number, interval: IntervalInt): List<Appointment> => {
      const intersectingBlocks = appointmentBlocks.filter(block =>
        areIntervalsIntersects(block.interval, interval)
      );
      const intersectingApps = intersectingBlocks.reduce(
        (acc: List<Appointment>, block: AppointmentsBlock) =>
          acc.concat(
            block.elements.filter(
              app =>
                app.rowIndex === row &&
                areIntervalsIntersects(app.interval, interval, false, false)
            )
          ),
        List.of()
      );

      return intersectingApps;
    },
    [appointmentBlocks]
  );

  const tryToFreePlace = useCallback(
    (
      forWhom: string,
      rowIndex: number,
      interval: IntervalInt
      ): [Result.Fail] | [Result.Success, List<AppointmentsBlock>, boolean] => {
      type ShiftNode = {
        value: Appointment;
        ifGoUp: List<ShiftNode> | false;
        ifGoDown: List<ShiftNode> | false;
      };

      const usedIds: Set<string> = new Set();

      const makeIdShorter = (id: string) => id.split("-")[0];
      const getIdNode = (node: ShiftNode) => makeIdShorter(node.value.id);

      const applyShiftNode = (
        node: ShiftNode,
        appointmentBlocks: List<AppointmentsBlock>
      ): List<AppointmentsBlock> => {
        const reducer = (acc: List<AppointmentsBlock>, val: ShiftNode) =>
          applyShiftNode(val, acc);
        if (node.ifGoUp) {
          if ((window as any).logging)
            console.log(`shift ${getIdNode(node)} up`);
          const onceShiftedAppBlocks = shiftAppForBlocks(
            node.value.id,
            -1,
            appointmentBlocks
          );
          const fullyShitedNewAppBlocks = node.ifGoUp.reduce(
            reducer,
            onceShiftedAppBlocks
          );
          return fullyShitedNewAppBlocks;
        } else if (node.ifGoDown) {
          if ((window as any).logging)
            console.log(`shift ${getIdNode(node)} down`);
          const onceShiftedAppBlocks = shiftAppForBlocks(
            node.value.id,
            1,
            appointmentBlocks
          );
          const fullyShitedNewAppBlocks = node.ifGoDown.reduce(
            reducer,
            onceShiftedAppBlocks
          );
          return fullyShitedNewAppBlocks;
        }

        throw new Error("This node is stop node");
      };

      const getNeighboursUnderAndAbove = (
        app: Appointment,
        appointmentBlocks: List<AppointmentsBlock>,
        exclude: string[]
      ): { under: List<Appointment>; above: List<Appointment> } => {
        const verticalNeighbours = getVerticalNeighboursByApp(
          app,
          appointmentBlocks
        ).filter(({ id }) => !exclude.includes(id));

        const under = verticalNeighbours.filter(
          nei => nei.rowIndex > app.rowIndex
        );
        const above = verticalNeighbours.filter(
          nei => nei.rowIndex < app.rowIndex
        );

        return { under, above };
      };

      const buildShiftNode = (
        app: Appointment,
        moverId: string,
        appointmentBlocks: List<AppointmentsBlock>
      ) => {
        if ((window as any).logging)
          console.log(
            `buildShiftNode for ${makeIdShorter(
              app.id
            )} (previous: ${makeIdShorter(moverId)})`
          );

        const node: ShiftNode = {
          value: app,
          ifGoUp: false, // nodes to shift if to go up
          ifGoDown: false // nodes to shift if to go down
        };

        return buildShiftTree(node, moverId, appointmentBlocks);
      };

      const buildShiftTree = (
        currentNode: ShiftNode,
        moverId: string,
        appointmentBlocks: List<AppointmentsBlock>
      ): ShiftNode | false => {
        if ((window as any).logging)
          console.log(
            `buildShiftTree for ${getIdNode(
              currentNode
            )} (previous: ${makeIdShorter(moverId)})`
          );

        const {
          above: appsAbove,
          under: appsUnder
        } = getNeighboursUnderAndAbove(
          currentNode.value,
          appointmentBlocks,
          []
        );
        const { value: currentApp } = currentNode;

        usedIds.add(currentApp.id);

        const nearToBorderTop = currentApp.rowIndex === 0;
        const nearToBorderBottom = currentApp.rowIndex === rowsCount - 1;

        const isStatic = (app: Appointment) =>
          usedIds.has(app.id) || app.id === forWhom;
        const isStaticTop = nearToBorderTop || appsAbove.some(isStatic);
        const isStaticBottom = nearToBorderBottom || appsUnder.some(isStatic);

        const reducer = (acc: List<ShiftNode> | false, val: Appointment) => {
          if (!acc || usedIds.has(val.id)) return false;

          const node = buildShiftNode(val, currentApp.id, appointmentBlocks);
          if (!node) return false;

          return acc.push(node);
        };

        const shiftsTop: List<ShiftNode> | false = isStaticTop
          ? false
          : appsAbove.reduce(reducer, List.of());
        const shiftsBottom: List<ShiftNode> | false = isStaticBottom
          ? false
          : appsUnder.reduce(reducer, List.of());

        // mutating
        currentNode.ifGoUp = shiftsTop;
        currentNode.ifGoDown = shiftsBottom;

        if ((window as any).logging)
          console.log(
            `${getIdNode(currentNode)} was build. up: ${shiftsTop &&
              shiftsTop.count()} down: ${shiftsBottom && shiftsBottom.count()}`
          );

        return !shiftsTop && !shiftsBottom ? false : currentNode;
      };

      // move draggingApp
      const appBlocksAfterDrop = updateAppForBlocks(
        { id: forWhom, interval, rowIndex },
        appointmentBlocks
      );

      // get all intersecting apps except dragging app
      const intersectingApps = getIntersectingApps(rowIndex, interval).filter(
        ({ id }) => id !== forWhom
      );

      if ((window as any).logging)
        console.log(`placing ${makeIdShorter(forWhom)}`);

      // if no intersectings - just return
      if (!intersectingApps.count())
        return [Result.Success, appBlocksAfterDrop, true];

      type ReduceState = List<AppointmentsBlock> | false;
      const result = intersectingApps.reduce(
        (appBlocks: ReduceState, val: Appointment) => {
          if (!appBlocks) return false;
          if ((window as any).logging)
            console.log(`shifting app ${makeIdShorter(val.id)}`);

          const rootShiftNode = buildShiftNode(val, forWhom, appBlocks);
          if (!rootShiftNode) return false;

          if ((window as any).logging) console.log({ rootShiftNode });

          const newAppBlocks = applyShiftNode(rootShiftNode, appBlocks);
          return newAppBlocks;
        },
        appBlocksAfterDrop
      );

      if (!result) return [Result.Fail];
      else return [Result.Success, result, false];
    },
    [
      getIntersectingApps,
      getVerticalNeighboursByApp,
      appointmentBlocks,
      rowsCount,
      shiftAppForBlocks,
      updateAppForBlocks
    ]
  );

  (window as any).appBlocks = appointmentBlocks;

  // console.log(
  //   appointmentBlocks.toArray().map(block => intervalToString(block.interval))
  // );

  return {
    appointmentBlocks,
    addAppointment,
    addAppointments,
    blockSize,
    getApp,
    getAppById,
    getAppCoords,
    updateApp,
    updateApps,
    getVerticalNeighbours: getVerticalNeighboursById,
    tryToFreePlace,
    setAppointmentBlocks
  };
}

export const AppointmentsInfo = createContainer(useAppointments);
