import './SettingsGroup.scss';

import React from 'react';

import { SettingsEntityPossible } from '../../../Calendar.containers';

export interface SettingsSubBlockProps {
  groupName: string;
  settings: SettingsEntityPossible[];
}

const SettingsGroup = ({ groupName, settings }: SettingsSubBlockProps) => {
  const onChangeSetterBoolean = (setter: (val: boolean) => void) => (
    event: React.ChangeEvent<HTMLInputElement>
  ) => setter(event.target.checked);

  const onChangeSetterNumber = (setter: (val: number) => void) => (
    event: React.ChangeEvent<HTMLInputElement>
  ) => setter(parseInt(event.target.value));

  return (
    <div styleName="settingsGroup">
      <div styleName="groupName">{groupName}</div>
      {settings.map(({ name, value, setter, disabled }) => {
        const setterElement =
          typeof value === "boolean" ? (
            <input
              type="checkbox"
              checked={value}
              disabled={disabled}
              onChange={onChangeSetterBoolean(setter)}
            />
          ) : (
            <input
              min={1}
              step={1}
              type="range"
              value={value}
              disabled={disabled}
              onChange={onChangeSetterNumber(setter)}
            />
          );

        return (
          <div key={name} styleName="entity">
            {name}
            {setterElement}
          </div>
        );
      })}
    </div>
  );
};

export default SettingsGroup;
