import { addDays, differenceInDays } from 'date-fns';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useDebounce, usePrevious, useWindowSize } from 'use-hooks';

import { StickyController } from '../../../../assembly/stickyController';
import { UIInfo } from '../../../../stores/ui';
import { GridInfo, ScrollInfo, ViewIntervalInfo } from '../../Calendar.containers';
import MonthChunk from './MonthChunk';
import styleVariables from './MonthRow.scss';

const monthChunkSize = parseFloat(styleVariables.monthChunkSize);
const monthChunksGap = parseFloat(styleVariables.monthChunksGap);
const monthChunksSizeTotal = monthChunkSize + monthChunksGap;

const MonthRow = () => {
  const [selfData, setSelfData] = useState({
    offsetHeight: 0,
    offsetWidth: 0
  });
  const { width } = useWindowSize();
  const widthD = useDebounce(width, 500);
  const {
    currentDay,
    currentDayAfterScrolling,
    initialStartOfDay
  } = ViewIntervalInfo.useContainer();
  const { pageLoaded } = UIInfo.useContainer();
  const { setZeroAxis, getStartOfWorkDay } = GridInfo.useContainer();
  const { resetScroll } = ScrollInfo.useContainer();

  const currentDayPrevious = usePrevious(currentDay);
  const daysDeltaSinceLast =
    differenceInDays(currentDay, currentDayPrevious) || 0;
  // console.log({ daysDeltaSinceLast });
  // const jumpDeltaPrevious = usePrevious(jumpDelta);

  // useEffect(
  //   () =>
  //     jumpDeltaPrevious !== 0 ? setJumpDelta(0) : undefined,
  //   [jumpDelta, jumpDeltaPrevious]
  // );

  const self = useRef<HTMLDivElement>(null);

  // self data collecting hook
  useEffect(() => {
    if (self.current) {
      const { offsetWidth, offsetHeight } = self.current;
      setSelfData({ offsetWidth, offsetHeight });
    }
  }, [widthD]);

  // sticky hook
  useEffect(() => {
    const element = self.current as HTMLDivElement;
    StickyController.addStickyElement(element, 0);
    StickyController.setTriggerScrollValue(element.offsetTop);
  }, []);

  const buffer = Math.max(1, Math.abs(daysDeltaSinceLast));

  // useEffect(() => (jumpDelta !== 0 ? setJumpDelta(0) : undefined), [jumpDelta]);

  // console.log(buffer, jumpDelta);

  const chunksCountVisible = Math.floor(
    selfData.offsetWidth / monthChunksSizeTotal
  );
  const chunksCountTotal = chunksCountVisible + buffer * 2;

  const daysDelta = pageLoaded
    ? differenceInDays(currentDay, initialStartOfDay)
    : 0;
  const daysDeltaAfterScrolling = pageLoaded
    ? differenceInDays(currentDayAfterScrolling, initialStartOfDay)
    : 0;

  const translateXAnimated = -daysDelta * monthChunksSizeTotal;
  const translateXInstant =
    (daysDeltaAfterScrolling - buffer) * monthChunksSizeTotal;

  const setCurrentDate = useCallback(
    (date: number) => {
      setZeroAxis(getStartOfWorkDay(date));
      resetScroll();
    },
    [setZeroAxis, resetScroll, getStartOfWorkDay]
  );

  // console.log({ buffer });
  // useWhyDidYouUpdate("monthrow", {
  //   chunksCountTotal,
  //   initialStartOfDay,
  //   translateXAnimated,
  //   translateXInstant,
  //   offsetWidth: selfData.offsetWidth,
  //   daysDeltaAfterScrolling,
  //   chunksCountVisible,
  //   currentDay,
  //   setCurrentDate,
  //   buffer
  // });

  const chunks = useMemo(() => {
    const currentDayDate = new Date(currentDay);

    return new Array(chunksCountTotal).fill(null).map((_, i) => {
      const date = addDays(
        initialStartOfDay,
        i +
          daysDeltaAfterScrolling -
          buffer -
          Math.floor(chunksCountVisible / 2)
      );
      const current =
        currentDayDate.getDate() === date.getDate() &&
        currentDayDate.getMonth() === date.getMonth();
      return (
        <MonthChunk
          key={date.valueOf()}
          date={date.valueOf()}
          solid={current}
          appsCount={169}
          setCurrentDate={setCurrentDate}
        />
      );
    });
  }, [
    buffer,
    chunksCountTotal,
    chunksCountVisible,
    currentDay,
    daysDeltaAfterScrolling,
    initialStartOfDay,
    setCurrentDate
  ]);

  return useMemo(() => {
    return (
      <div
        styleName="monthRow"
        ref={self}
        style={{
          transform: `translateX(${translateXInstant}px)`
        }}
      >
        <div
          styleName="offsetController"
          style={{
            transform: `translateX(${translateXAnimated}px)`,
            width: selfData.offsetWidth + buffer * 2 * monthChunksSizeTotal
          }}
        >
          {chunks}
        </div>
      </div>
    );
  }, [
    translateXAnimated,
    translateXInstant,
    selfData.offsetWidth,
    chunks,
    buffer
  ]);
};

export default MonthRow;
