function makeElementsSticky(elements: HTMLElement[]): void {
  elements.reduce((offsetTop: number, element) => {
    element.style.position = "fixed";
    element.style.top = `${offsetTop}px`;

    return offsetTop + element.offsetHeight;
  }, 0);
}

function makeElementsNonSticky(elements: HTMLElement[]): void {
  elements.forEach(element => {
    element.style.position = "";
    element.style.top = "";
  });
}

export const StickyController = (function() {
  const elements: HTMLElement[] = [];
  let triggerScrollValue: number | null = null;

  window.addEventListener("scroll", () => {
    if (triggerScrollValue === null) return;

    const { scrollY } = window;
    if (scrollY >= triggerScrollValue) makeElementsSticky(elements);
    else makeElementsNonSticky(elements);
  });

  return {
    addStickyElement: (element: HTMLElement, index: number) =>
      (elements[index] = element),
    setTriggerScrollValue: (value: number) => (triggerScrollValue = value)
  };
})();
