import './MainBlock.scss';

import { addDays, differenceInDays, getDay, startOfDay } from 'date-fns';
import { List } from 'immutable';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import { useDebounce, useWindowSize } from 'use-hooks';

import { addAnimation, removeAnimation } from '../../../assembly/animationController';
import { areIntervalsIntersects, numberizeInterval, offsetInterval } from '../../../assembly/interval';
import { convertCustomMs } from '../../../assembly/timeConverts';
import { Appointment, AppointmentsBlock, AppointmentsInfo, Result } from '../../../stores/appointments';
import {
  ColumnsInfo,
  DraggedInfo,
  DraggingAutoScrollInfo,
  DragSettingsInfo,
  GridInfo,
  RestrictedZonesInfo,
  RowBlocksInfo,
  RowsInfo,
  ScrollInfo,
  TimeLineInfo,
  Weekday,
} from '../Calendar.containers';
import ScrollingLayer from './ScrollingLayer';
import StaticLayer from './StaticLayer';

export interface Props {
  // scrollOffset: number;
  minColumnWidth: number;
}

interface SelfData {
  offsetWidth: number;
  offsetHeight: number;
  offsetLeft: number;
  offsetTop: number;
}

const MainBlock = ({ minColumnWidth }: Props) => {
  const { setColumns, columnWidth } = ColumnsInfo.useContainer();
  const { rowHeight, rowsCount } = RowsInfo.useContainer();
  const { restrictedZones } = RestrictedZonesInfo.useContainer();
  const { rowBlocks, indexesMap } = RowBlocksInfo.useContainer();
  const {
    scrollOffset,
    oneScrollSizePixels,
    isScrolling,
    scrollLeft,
    scrollRight
  } = ScrollInfo.useContainer();
  const {
    draggingApp,
    setDraggingApp,
    dragOffsetToAppCell,
    isDragging
  } = DraggedInfo.useContainer();
  const { timeLineHeight } = TimeLineInfo.useContainer();
  const {
    getApp,
    tryToFreePlace,
    setAppointmentBlocks
  } = AppointmentsInfo.useContainer();
  const {
    zeroAxis,
    gridStepDuration,
    oneDayDurationBlind,
    oneDayDuration,
    getEndOfWorkDay
  } = GridInfo.useContainer();
  const {
    thresholdPixelsHorizontal,
    thresholdPixelsVertical
  } = DraggingAutoScrollInfo.useContainer();

  const {
    stateOnDragging: {
      state: [onDragging]
    },
    stateOnDraggingApart: {
      state: [onDraggingApart]
    }
  } = DragSettingsInfo.useContainer();

  const { width } = useWindowSize();
  const widthD = useDebounce(width, 500);

  const [selfData, setSelfData] = useState<SelfData>({
    offsetHeight: 0,
    offsetWidth: 0,
    offsetLeft: 0,
    offsetTop: 0
  });
  const self = useRef<HTMLDivElement>(null);
  const [dragStartScrollY, setDragStartScrollY] = useState(0);

  const loaded = !!selfData.offsetWidth;

  // self data collecting hook
  useEffect(() => {
    if (self.current) {
      const { offsetWidth, offsetHeight, offsetTop, offsetLeft } = self.current;
      setSelfData({ offsetWidth, offsetHeight, offsetTop, offsetLeft });
    }
  }, [widthD, self]);

  useEffect(() => {
    if (loaded) setColumns(selfData.offsetWidth, minColumnWidth);
  }, [selfData.offsetWidth, minColumnWidth, loaded, setColumns]);

  const [relativeTriggerLeft, setRelativeTriggerLeft] = useState(Infinity);
  const [relativeTriggerRight, setRelativeTriggerRight] = useState(Infinity);
  const [relativeTriggerTop, setRelativeTriggerTop] = useState(Infinity);
  const [relativeTriggerBottom, setRelativeTriggerBottom] = useState(Infinity);
  const [anyAnimationActive, setAnyAnimationActive] = useState(false);

  const scrollAnimationTop = useCallback(
    (delta: number) => window.scrollBy(0, -1 * delta),
    []
  );
  const scrollAnimationBottom = useCallback(
    (delta: number) => window.scrollBy(0, 1 * delta),
    []
  );

  const clearAnimations = useCallback(() => {
    removeAnimation(scrollAnimationTop);
    removeAnimation(scrollAnimationBottom);

    setAnyAnimationActive(false);
  }, [scrollAnimationTop, scrollAnimationBottom]);

  const tryClearAnimations = useCallback(() => {
    if (anyAnimationActive) clearAnimations();
  }, [anyAnimationActive, clearAnimations]);

  if (!isScrolling && isDragging) {
    if (relativeTriggerLeft < thresholdPixelsHorizontal) scrollLeft();
    if (relativeTriggerRight < thresholdPixelsHorizontal) scrollRight();

    if (relativeTriggerTop < thresholdPixelsVertical) {
      if (!anyAnimationActive) {
        addAnimation(scrollAnimationTop);
        setAnyAnimationActive(true);
      }
    } else if (relativeTriggerBottom < thresholdPixelsVertical) {
      if (!anyAnimationActive) {
        addAnimation(scrollAnimationBottom);
        setAnyAnimationActive(true);
      }
    } else tryClearAnimations();
  } else tryClearAnimations();

  const getRowColumn = useCallback(
    (pageX: number, pageY: number, processor = (a: number) => a) => {
      const realX = pageX - selfData.offsetLeft;
      const realY = pageY - selfData.offsetTop;

      const absoluteX = realX - scrollOffset * oneScrollSizePixels;
      const absoluteY = realY - timeLineHeight;

      const row = processor(absoluteY / rowHeight);
      const column = processor(absoluteX / columnWidth);

      const rowNormalized = indexesMap[row];

      const rowNormalizedNormalized = Math.min(
        Math.max(rowNormalized, 0),
        rowsCount
      );
      const columnNormalized = column;

      return {
        row: rowNormalizedNormalized,
        column: columnNormalized,
        outOfBoundary: rowNormalizedNormalized !== rowNormalized
      };
    },
    [
      selfData,
      scrollOffset,
      oneScrollSizePixels,
      rowHeight,
      columnWidth,
      timeLineHeight,
      rowsCount,
      indexesMap
    ]
  );

  const getDate = useCallback(
    (column: number) => {
      const offsetDuration = column * gridStepDuration.valueOf();
      const daysOffset = convertCustomMs(offsetDuration, oneDayDuration);
      const daysDurationAddition = daysOffset * oneDayDurationBlind;
      return zeroAxis.valueOf() + offsetDuration + daysDurationAddition;
    },
    [zeroAxis, gridStepDuration, oneDayDuration, oneDayDurationBlind]
  );

  const mouseDownHandler = useCallback(
    (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      if (!onDragging) return;

      const { pageX, pageY } = event;
      const { column, row, outOfBoundary } = getRowColumn(
        pageX,
        pageY,
        Math.floor
      );

      if (outOfBoundary) return;

      const date = getDate(column);
      const app = getApp(date, row);
      // console.log({ row, column, app, outOfBoundary });

      if (app) {
        setDraggingApp(app);
        setDragStartScrollY(window.scrollY);
      }
    },
    [getRowColumn, getApp, setDraggingApp, getDate, onDragging]
  );

  const mouseUpHandler = useCallback(
    (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      // console.log("mouse up", draggingApp);
      if (!draggingApp) return;
      //  console.log("mouse up (with draggingApp)");

      const { pageX, pageY } = event;
      const { dx, dy } = dragOffsetToAppCell;
      const deltaScrollY = window.scrollY - dragStartScrollY;
      const anchorX = pageX - dx;
      const anchorY = pageY - dy + window.scrollY - deltaScrollY; //  + rowHeight / 2;

      const { row, column } = getRowColumn(anchorX, anchorY, Math.round);

      const currentDateStart = getDate(column);
      const dateOffset = currentDateStart - draggingApp.interval.start;
      const newInterval = offsetInterval(draggingApp.interval, dateOffset);

      // check if column is restricted
      const weekday: Weekday = getDay(currentDateStart);
      const zones = restrictedZones[weekday];
      const daysOffset = zones.length
        ? differenceInDays(
            startOfDay(currentDateStart),
            startOfDay(zones[0].start)
          )
        : 0;

      const zonesNormalized = zones.map(
        zone =>
          ({
            start: addDays(zone.start, daysOffset),
            end: addDays(zone.end, daysOffset)
          } as Interval)
      );
      const restricted = zonesNormalized.reduce(
        (restrict, zone) =>
          restrict
            ? true
            : areIntervalsIntersects(
                numberizeInterval(zone),
                newInterval,
                false,
                false
              ),
        false
      );
      if (restricted) return;

      const overlapsNextDay =
        newInterval.end > getEndOfWorkDay(newInterval.start).valueOf();
      // console.log(
      //   [newInterval.end, getEndOfWorkDay(newInterval.start)].map(date =>
      //     format(date, "dd HH:mm")
      //   )
      // );

      if (overlapsNextDay) return;

      const updatedApp: Appointment = {
        ...draggingApp,
        rowIndex: row,
        interval: newInterval
      };

      const [shiftResult, newAppBlocks, nothingChanged] = tryToFreePlace(
        updatedApp.id,
        updatedApp.rowIndex,
        updatedApp.interval
      );

      if (shiftResult === Result.Success && (nothingChanged || onDraggingApart))
        setAppointmentBlocks(newAppBlocks as List<AppointmentsBlock>);

      setDraggingApp(null);
      setRelativeTriggerLeft(Infinity);
      setRelativeTriggerRight(Infinity);
      setRelativeTriggerTop(Infinity);
      setRelativeTriggerBottom(Infinity);
    },
    [
      draggingApp,
      getRowColumn,
      getDate,
      setDraggingApp,
      dragOffsetToAppCell,
      tryToFreePlace,
      setAppointmentBlocks,
      dragStartScrollY,
      restrictedZones,
      getEndOfWorkDay,
      onDraggingApart
    ]
  );

  const mouseMoveHandler = useCallback(
    (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      if (!isDragging) return;

      const relativeTriggerLeft = event.clientX;
      const relativeTriggerRight = window.innerWidth - relativeTriggerLeft;

      const relativeTriggerTop = event.clientY;
      const relativeTriggerBottom = window.innerHeight - relativeTriggerTop;

      setRelativeTriggerLeft(relativeTriggerLeft);
      setRelativeTriggerRight(relativeTriggerRight);
      setRelativeTriggerTop(relativeTriggerTop);
      setRelativeTriggerBottom(relativeTriggerBottom);
    },
    [isDragging]
  );

  // console.log(selfData);

  const innerJSX = useMemo(
    () => (
      <>
        <ScrollingLayer />
        <StaticLayer />
      </>
    ),
    []
  );

  const height = rowHeight * (rowsCount + 2 + rowBlocks.length) + 1;

  return useMemo(
    () => (
      <div
        styleName="mainBlock"
        ref={self}
        onMouseDownCapture={mouseDownHandler}
        onMouseUpCapture={mouseUpHandler}
        onMouseMoveCapture={mouseMoveHandler}
        onDropCapture={mouseUpHandler}
        style={{ height }}
      >
        {innerJSX}
      </div>
    ),
    [mouseDownHandler, mouseUpHandler, mouseMoveHandler, innerJSX, height]
  );
};

export default MainBlock;
