import { areIntervalsOverlapping } from 'date-fns';
import { v4 } from 'uuid';

import { intervalToString } from '../assembly/interval';
import { randomInt } from '../assembly/random';
import { timeout } from '../assembly/timeout';
import { shell } from '../assembly/utility';
import { AppointmentUnassigned, createAppointment } from '../stores/appointments';

const processedIntervals = new Map<number, AppointmentUnassigned[]>();

export function isIntervalAlreadyFetched(dayIntervals: Interval) {
  return processedIntervals.has(dayIntervals.start.valueOf());
}

export function fetchAppointmentsAtInterval(
  dayInterval: Interval,
  returnAlreadyFetched: boolean,
  toGenerate: number,
  stepDuration: Date | number = 1,
  maxRowIndex = 20
) {
  function snapValue(val: number): number {
    const a = val / stepDuration.valueOf();
    const b = Math.floor(a);
    // const ad = new Date(a);
    // const bd = new Date(b);
    // console.log({
    //   val,
    //   a,
    //   b,
    //   ad,
    //   bd,
    //   stepDuration: stepDuration.valueOf(),
    //   res: b * stepDuration.valueOf(),
    //   resd: new Date(b * stepDuration.valueOf())
    // });
    return b * stepDuration.valueOf();
  }

  const possibleDuration: Interval = {
    end: stepDuration.valueOf() * 2,
    start: stepDuration.valueOf()
  };

  const dayIntervalStart = dayInterval.start.valueOf();
  if (processedIntervals.has(dayIntervalStart))
    if (returnAlreadyFetched)
      return processedIntervals.get(
        dayIntervalStart
      ) as AppointmentUnassigned[];
    else return [];

  const intervalDuration =
    dayInterval.end.valueOf() - dayInterval.start.valueOf();
  const apps = shell(toGenerate)
    .map(() => {
      return (
        dayInterval.start.valueOf() +
        snapValue(
          randomInt(
            0,
            Math.max(0, intervalDuration - possibleDuration.start.valueOf())
          )
        )
      );
    })
    .map(start => {
      const duration = snapValue(
        randomInt(
          possibleDuration.start,
          possibleDuration.end.valueOf() +
            (possibleDuration.end.valueOf() - possibleDuration.start.valueOf())
        )
      );
      const end = Math.min(start + duration, dayInterval.end.valueOf());
      const interval = { start, end };

      // console.log(format(interval.start, "dd HH:mm"));

      return createAppointment({
        interval,
        rowIndex: randomInt(0, maxRowIndex),
        userId: v4()
      });
    })
    .reduce(
      (acc: AppointmentUnassigned[], app) =>
        acc.some(
          app2 =>
            app2.rowIndex === app.rowIndex &&
            areIntervalsOverlapping(app.interval, app2.interval)
        )
          ? acc
          : [...acc, app],
      []
    );

  processedIntervals.set(dayIntervalStart, apps);
  console.log(
    `${apps.length} apps fetched for \n${intervalToString(dayInterval)}`
  );

  return apps;
}

export async function fetchAppointments(
  intervals: Interval[],
  stepDuration: Date | number = 1,
  densityPerDay = 200,
  maxRowIndex = 10,
  returnAlreadyFetched = false
): Promise<AppointmentUnassigned[]> {
  const delay = 0;

  await timeout(delay);

  // function toGMT(date: Date) {
  //   return addMinutes(date, date.getTimezoneOffset());
  // }

  const intervalsFilled = intervals.map(interval => {
    // const intervalGMT: Interval = {
    //   start: toGMT(new Date(interval.start)),
    //   end: toGMT(new Date(interval.end))
    // };
    // console.log(
    //   intervalToString(interval),
    //   "=>",
    //   intervalToString(intervalGMT)
    // );
    return fetchAppointmentsAtInterval(
      interval,
      returnAlreadyFetched,
      densityPerDay,
      stepDuration,
      maxRowIndex
    );
  });

  const apps = intervalsFilled.reduce((acc, val) => [...acc, ...val]);

  return apps;
}

export default 0;
