const initialTime = new Date().valueOf();
const initialStartOfDay = startOfDay(initialTime);

function useLeftColumn() {
  const [leftColumnWidth, setLeftColumnWidth] = useState(50);
  return { leftColumnWidth, setLeftColumnWidth };
}

function useColumns() {
  const [columnWidth, setColumnWidth] = useState(0);
  const [columnsCount, setColumnsCount] = useState(0);
  const [previousMinColumnWidth, setPreviousMinColumnWidth] = useState(0);
  const [previousContainerWidth, setPreviousContainerWidth] = useState(0);

  const setColumns = (containerWidth: number, minColumnWidth: number) => {
    const newColumnsCount = Math.floor(containerWidth / minColumnWidth);
    const newColumnWidth = containerWidth / newColumnsCount;

    setColumnsCount(newColumnsCount);
    setColumnWidth(newColumnWidth);

    if (containerWidth !== previousContainerWidth)
      setPreviousContainerWidth(containerWidth);
  };

  const setMinColumnWidth = (minColumnWidth: number) => {
    setPreviousMinColumnWidth(minColumnWidth);
    setColumns(previousContainerWidth, minColumnWidth);
  };

  return {
    columnWidth,
    columnsCount,
    setColumns,
    minColumnWidth: previousMinColumnWidth,
    setMinColumnWidth
  };
}

function useRows() {
  const [rowHeight, setRowHeight] = useState(
    parseFloat(styleVariables.rowHeight)
  );
  const [rowsCount, setRowsCount] = useState(40);

  const setRows = (newRowsCount: number, newRowHeight: number) => {
    setRowsCount(newRowsCount);
    setRowHeight(newRowHeight);
  };

  return { rowHeight, rowsCount, setRows };
}

function useTimeLine() {
  const { rowHeight } = RowsInfo.useContainer();

  const [timeLineHeight, setTimeLineHeight] = useState(rowHeight);

  return { timeLineHeight, setTimeLineHeight };
}

function useScroll() {
  performance.mark("scroll container update start");
  const { columnWidth } = ColumnsInfo.useContainer();

  const [scrollOffset, setScrollOffset] = useState(0);
  const oneScrollSize = useMedia(["(max-width: 800px)"], [1], 0.5);
  const [isScrolling, setIsScrolling] = useState(false);
  const [scrollDuration] = useState(0.5);
  const [oneScrollSizePixels, setOneScrollSizePixels] = useState(0);
  const [oneScrollSizeReal, setOneScrollSizeReal] = useState(0);
  const [lastScrollDirection, setLastScrollDirection] = useState(0);
  const [scrollIsJustReset, setScrollIsJustReset] = useState(false);

  const scrollIsJustResetPrevious = (usePrevious(
    scrollIsJustReset
  ) as any) as boolean;
  if (scrollIsJustResetPrevious && scrollIsJustReset)
    setScrollIsJustReset(false);

  // console.log({ scrollIsJustReset, scrollIsJustResetPrevious });

  const [scrollOffsetDone, setScrollOffsetDone] = useState(scrollOffset);

  const columnsPerScroll = Math.round(oneScrollSizePixels / columnWidth) || 0;

  const resetScroll = useCallback(() => {
    setScrollOffset(0);
    setScrollOffsetDone(0);
    setScrollIsJustReset(true);
  }, []);

  const scrollingEventsGenerator = useMemo(
    () =>
      (function*(): Iterator<
        { justEnded: boolean; justStarted: boolean },
        { justEnded: boolean; justStarted: boolean },
        boolean
      > {
        let wasScrolling = false;
        let justEnded = false;
        let justStarted = false;
        let currentScrolling = false;

        while (true) {
          currentScrolling = yield {
            justEnded,
            justStarted
          };

          justStarted = currentScrolling && !wasScrolling;
          justEnded = !currentScrolling && wasScrolling;

          wasScrolling = currentScrolling;
        }
      })(),
    []
  );

  const {
    justStarted: scrollingJustStarted,
    justEnded: scrollingJustEnded
  } = scrollingEventsGenerator.next(isScrolling).value;

  const pageWidth = oneScrollSizePixels / oneScrollSizeReal;

  const scrollRight = () => {
    setScrollOffset(scrollOffset - 1);
    setIsScrolling(true);
    setLastScrollDirection(1);
  };

  const scrollLeft = () => {
    setScrollOffset(scrollOffset + 1);
    setIsScrolling(true);
    setLastScrollDirection(-1);
  };

  const endScroll = () => {
    performance.mark("endScroll start");

    performance.mark("setIsScrolling start");
    setIsScrolling(false);
    performance.mark("setIsScrolling end");

    // just perform it at the next tick (to avoid lagg)
    setTimeout(() => {
      performance.mark("setScrollOffsetDone start");
      setScrollOffsetDone(scrollOffset);
      performance.mark("setScrollOffsetDone end");

      performance.mark("endScroll end");
      performance.measure("endScroll", "endScroll start", "endScroll end");

      performance.measure(
        "setScrollOffsetDone",
        "setScrollOffsetDone start",
        "setScrollOffsetDone end"
      );
    });

    performance.measure(
      "setIsScrolling",
      "setIsScrolling start",
      "setIsScrolling end"
    );
  };

  performance.mark("scroll container update end");
  performance.measure(
    "scroll container update",
    "scroll container update start",
    "scroll container update end"
  );

  return {
    endScroll,
    isScrolling,
    oneScrollSize,
    oneScrollSizePixels,
    oneScrollSizeReal,
    pageWidth,
    scrollDuration,
    scrollingJustEnded,
    scrollingJustStarted,
    scrollLeft,
    columnsPerScroll,
    scrollOffset,
    scrollRight,
    setOneScrollSizePixels,
    setOneScrollSizeReal,
    lastScrollDirection,
    scrollOffsetDone,
    resetScroll,
    scrollIsJustReset
  };
}

function useGrid() {
  const getStartOfWorkDay = useCallback((date: Date | number) => {
    return addHours(addMinutes(startOfDay(date), 0), 7);
  }, []);

  const [gridStepDuration, setGridStepDuration] = useState(addMinutes(0, 40));
  const [oneDayStartTime, setOneDayStartTime] = useState(
    getStartOfWorkDay(initialTime)
  );

  const [oneDayStepsCount, setOneDayStepsCount] = useState(10);

  const oneDayDuration = oneDayStepsCount * gridStepDuration.valueOf();
  const oneDayDurationBlind =
    convert(1, Duration.day, Duration.ms) - oneDayDuration;

  const getEndOfWorkDay = useCallback(
    (date: Date | number) => {
      return addMilliseconds(getStartOfWorkDay(date), oneDayDuration);
    },
    [oneDayDuration, getStartOfWorkDay]
  );

  const initialZeroAxis = oneDayStartTime;
  const [zeroAxis, setZeroAxis] = useState<Date | number>(initialZeroAxis);

  return {
    initialZeroAxis,
    gridStepDuration,
    setGridStepDuration,
    setZeroAxis,
    zeroAxis,
    oneDayStartTime,
    oneDayStepsCount,
    setOneDayStartTime,
    setOneDayStepsCount,
    oneDayDuration,
    oneDayDurationBlind,
    getStartOfWorkDay,
    getEndOfWorkDay
  };
}

function useViewInterval() {
  const {
    zeroAxis,
    gridStepDuration,
    oneDayDuration
  } = GridInfo.useContainer();
  const {
    scrollOffset,
    oneScrollSizeReal,
    pageWidth,
    isScrolling,
    scrollIsJustReset
  } = ScrollInfo.useContainer();
  const { columnWidth } = ColumnsInfo.useContainer();
  const { pageLoaded } = UIInfo.useContainer();

  const pageLoadedPrevious = (usePrevious(pageLoaded) as any) as boolean;
  const pageFirstLoaded = pageLoaded !== pageLoadedPrevious;

  const isScrollingPrevious = (usePrevious(isScrolling) as any) as boolean;
  const pageScrollingEnded = isScrollingPrevious && !isScrolling;

  const onePageDuration =
    Math.round(pageWidth / columnWidth) * gridStepDuration.valueOf() ||
    oneDayDuration;
  const oneScrollDuration = onePageDuration * oneScrollSizeReal;

  const normalizeTimestamp = useMemo(
    () => (timestamp: number) => {
      const daysOffset = convertCustom(timestamp, 1, oneDayDuration);
      const daysOffsetDuration = convertCustom(daysOffset, oneDayDuration, 1);
      const daysOffsetDurationNormalized = convert(
        daysOffset,
        Duration.day,
        Duration.ms
      );
      const remainOffsetDuration = timestamp - daysOffsetDuration;

      const totalOffsetDurationNormalized =
        daysOffsetDurationNormalized + remainOffsetDuration || 0;

      return totalOffsetDurationNormalized;
    },
    [oneDayDuration]
  );

  const totalOffsetDuration = -scrollOffset * oneScrollDuration;
  const totalOffsetDurationEnd = totalOffsetDuration + onePageDuration;

  const totalOffsetDurationNormalized = normalizeTimestamp(totalOffsetDuration);
  const totalOffsetDurationEndNormalized = normalizeTimestamp(
    totalOffsetDurationEnd
  );

  const viewInterval: IntervalInt = {
    start: addMilliseconds(zeroAxis, totalOffsetDurationNormalized).valueOf(),
    end: addMilliseconds(zeroAxis, totalOffsetDurationEndNormalized).valueOf()
  };

  const [viewIntervalAfterScrolling, setViewIntervalAfterScrolling] = useState(
    viewInterval
  );

  const viewIntervalMax = maxInterval(viewInterval, viewIntervalAfterScrolling);

  // console.log(
  //   `view: ${format(viewIntervalMax.start, "dd HH:mm")} - ${format(
  //     viewIntervalMax.end,
  //     "dd HH:mm"
  //   )}`
  // );

  // console.log(
  //   `view: ${format(viewInterval.start, "dd HH:mm")} - ${format(
  //     viewInterval.end,
  //     "dd HH:mm"
  //   )}`
  // );

  // console.log(
  //   `view2: ${format(viewIntervalAfterScrolling.start, "dd HH:mm")} - ${format(
  //     viewIntervalAfterScrolling.end,
  //     "dd HH:mm"
  //   )}`
  // );

  const currentDay = useMemo(
    () =>
      startOfDay(
        viewInterval.start + (viewInterval.end - viewInterval.start) / 2 || 0
      ).valueOf(),
    [viewInterval]
  );

  const currentDayAfterScrolling = useMemo(
    () =>
      startOfDay(
        viewIntervalAfterScrolling.start +
          (viewIntervalAfterScrolling.end - viewIntervalAfterScrolling.start) /
            2 || 0
      ).valueOf(),
    [viewIntervalAfterScrolling]
  );

  // console.log(`currentDay: ${format(currentDay, "dd")}`);

  // console.log(
  //   pageFirstLoaded || pageScrollingEnded || scrollIsJustReset,
  //   !isEqual(viewIntervalAfterScrolling.start, viewInterval.start) ||
  //     !isEqual(viewIntervalAfterScrolling.end, viewInterval.end)
  // );
  if (
    (pageFirstLoaded || pageScrollingEnded || scrollIsJustReset) &&
    (!isEqual(viewIntervalAfterScrolling.start, viewInterval.start) ||
      !isEqual(viewIntervalAfterScrolling.end, viewInterval.end))
  )
    setViewIntervalAfterScrolling(viewInterval);

  return {
    onePageDuration,
    oneScrollDuration,
    viewInterval,
    viewIntervalAfterScrolling,
    currentDay,
    currentDayAfterScrolling,
    initialTime,
    initialStartOfDay,
    viewIntervalMax
  };
}

function useDragged() {
  const [draggingApp, setDraggingApp] = useState<Appointment | null>(null);
  const [dragOffsetToAppCell, setDragOffsetToAppCell] = useState({
    dx: 0,
    dy: 0
  });
  const isDragging = draggingApp !== null;

  return {
    isDragging,
    draggingApp,
    setDraggingApp,
    dragOffsetToAppCell,
    setDragOffsetToAppCell
  };
}

function useDraggingAutoScroll() {
  const { columnWidth } = ColumnsInfo.useContainer();
  const { rowHeight } = RowsInfo.useContainer();
  const [threshold, setThreshold] = useState(0.3);
  const thresholdPixelsHorizontal = columnWidth * threshold;
  const thresholdPixelsVertical = rowHeight * threshold;

  return { thresholdPixelsVertical, thresholdPixelsHorizontal, setThreshold };
}

export enum Weekday {
  Sunday,
  Monday,
  Tuesday,
  Wednesday,
  Thursday,
  Friday,
  Saturday
}
export const RestrictedZonesInfo = createContainer(() => {
  type WeekZones = { [key in Weekday]: Interval[] };
  const [restrictedZones, setRestrictedZones] = useState<WeekZones>({
    [Weekday.Sunday]: [],
    [Weekday.Monday]: [
      {
        start: addMinutes(addHours(initialStartOfDay, 11), 0),
        end: addMinutes(addHours(initialStartOfDay, 12), 20)
      }
    ],
    [Weekday.Tuesday]: [
      {
        start: addMinutes(addHours(initialStartOfDay, 8), 20),
        end: addMinutes(addHours(initialStartOfDay, 9), 40)
      }
    ],
    [Weekday.Wednesday]: [
      {
        start: addMinutes(addHours(initialStartOfDay, 7), 0),
        end: addMinutes(addHours(initialStartOfDay, 9), 0)
      },
      {
        start: addMinutes(addHours(initialStartOfDay, 10), 20),
        end: addMinutes(addHours(initialStartOfDay, 11), 0)
      }
    ],
    [Weekday.Thursday]: [
      {
        start: addMinutes(addHours(initialStartOfDay, 7), 0),
        end: addMinutes(addHours(initialStartOfDay, 7), 40)
      },
      {
        start: addMinutes(addHours(initialStartOfDay, 10), 20),
        end: addMinutes(addHours(initialStartOfDay, 11), 40)
      }
    ],
    [Weekday.Friday]: [
      {
        start: addMinutes(addHours(initialStartOfDay, 8), 20),
        end: addMinutes(addHours(initialStartOfDay, 9), 40)
      }
    ],
    [Weekday.Saturday]: []
  });

  return { restrictedZones, setRestrictedZones };
});

export type RowBlock = IntervalInt & { name: string };
export const RowBlocksInfo = createContainer(() => {
  const { rowsCount } = RowsInfo.useContainer();
  const [rowBlocks, setRowBlocks] = useState<RowBlock[]>([
    { start: 0, end: 3, name: "Just the first block" },
    { start: 4, end: 5, name: "Just the second block" },
    { start: 6, end: 8, name: "Or third one?" },
    { start: 9, end: 13, name: "Ok, that's the last one" },
    { start: 14, end: 36, name: "Or not?" },
    { start: 37, end: 40, name: "Or not not?" }
  ]);

  const indexesMap = useMemo(() => {
    let counter = 0;
    let wasPrevious = false;
    const result = shell(rowsCount + rowBlocks.length + 2).reduce(
      (acc: number[]) => {
        const isStartOfSomething =
          rowBlocks.some(block => block.start === counter) && !wasPrevious;
        wasPrevious = isStartOfSomething;

        const newAcc = [...acc, counter];
        if (!isStartOfSomething) counter++;

        return newAcc;
      },
      []
    );

    return result;
  }, [rowsCount, rowBlocks]);

  return { rowBlocks, setRowBlocks, indexesMap };
});

export interface NamedState<T> {
  name: string;
  state: [T, Dispatch<SetStateAction<T>>];
}

