export type SettingsEntityPossible = SettingsEntity<boolean | number>;

export interface Settings {
  groupName: string;
  settings: SettingsEntityPossible[];
}

export interface SettingsEntity<T> {
  name: string;
  value: T;
  disabled?: boolean;
  setter: (val: T) => void;
}

export const SettingsInfo = createContainer(() => {
  const settingsDrag = DragSettingsInfo.useContainer();
  const settingsApp = AppointmentSettingsInfo.useContainer();
  const settingsGrid = GridSettingsInfo.useContainer();

  type SettingKeys =
    | keyof typeof settingsDrag
    | keyof typeof settingsApp
    | keyof typeof settingsGrid;
  const allSettings = { ...settingsDrag, ...settingsApp, ...settingsGrid };

  const dependencies: Record<SettingKeys, SettingKeys[]> = {
    stateMinColumnWidth: [],
    stateOnDragging: [],
    stateVisibilityAvatar: [],
    stateVisibilityVisits: [],
    stateOnDraggingApart: ["stateOnDragging"],
    stateOnDraggingApartCrossBlock: ["stateOnDragging", "stateOnDraggingApart"]
  };

  const checkIfDisabled = (name: SettingKeys) =>
    dependencies[name].some(depName => !allSettings[depName].state[0]);

  const state2entity = ([
    key,
    {
      name,
      state: [value, setter]
    }
  ]: [
    string,
    NamedState<boolean> | NamedState<number>
  ]): SettingsEntityPossible => ({
    name,
    value,
    setter: setter as (val: number | boolean) => void,
    disabled: checkIfDisabled(key as SettingKeys)
  });

  const normalizeSettings = (
    settings: Record<string, NamedState<boolean> | NamedState<number>>
  ) => Object.entries(settings).map(state2entity);
  const settings: Settings[] = [
    {
      groupName: "Dragging",
      settings: normalizeSettings(settingsDrag)
    },
    {
      groupName: "Appointment",
      settings: normalizeSettings(settingsApp)
    },
    {
      groupName: "Grid",
      settings: normalizeSettings(settingsGrid)
    }
  ];

  return settings;
});

