import './SettingsBlock.scss';

import React from 'react';

import { SettingsInfo } from '../../Calendar.containers';
import SettingsGroup, {  } from './SettingsGroup/SettingsGroup';

const SettingsBlock = () => {
  const settings = SettingsInfo.useContainer();

  return (
    <div styleName="settingsBlock">
      {settings.map(({groupName,settings}) => <SettingsGroup key={groupName} groupName={groupName} settings={settings} />)}      
    </div>
  );
};

export default SettingsBlock;
