import './LeftBlock.scss';

import classnames from 'classnames';
import React, { useMemo } from 'react';

import { RowBlocksInfo, RowsInfo } from '../Calendar.containers';

const LeftBlock = () => {
  const { rowsCount, rowHeight } = RowsInfo.useContainer();
  const { indexesMap } = RowBlocksInfo.useContainer();

  return useMemo(
    () => (
      <div styleName="leftBlock">
        {indexesMap.map((index, i) => {
          const isBlank = indexesMap[i - 1] === index;
          return (
            <span
              styleName={classnames(
                "chunk",
                i === 0 ? "first" : "",
                isBlank ? "blank" : ""
              )}
              key={i}
              style={{ height: rowHeight }}
            >
              {index - 1}
            </span>
          );
        })}
      </div>
    ),
    [rowHeight, indexesMap]
  );
};

export default LeftBlock;
