import { format, Interval } from 'date-fns';

export interface IntervalInt extends Interval {
  start: number;
  end: number;
}

export function numberizeInterval(interval: Interval): IntervalInt {
  return { start: interval.start.valueOf(), end: interval.end.valueOf() };
}

export function maxInterval(i1: IntervalInt, i2: IntervalInt): IntervalInt {
  return {
    end: Math.max(i1.end, i2.end),
    start: Math.min(i1.start, i2.start)
  };
}

export function withinInterval(
  val: Date | number,
  i: Interval,
  includeStart = true,
  includeEnd = true
): boolean {
  return (
    (includeStart ? i.start <= val : i.start < val) &&
    (includeEnd ? i.end >= val : i.end > val)
  );
}

export function offsetInterval(
  i: IntervalInt,
  delta: Date | number
): IntervalInt {
  return {
    start: i.start.valueOf() + delta.valueOf(),
    end: i.end.valueOf() + delta.valueOf()
  };
}

export function areIntervalsIntersects(
  i1: IntervalInt,
  i2: IntervalInt,
  includeStart = true,
  includeEnd = true
): boolean {
  return (
    (i1.start == i2.start && i1.end == i2.end) ||
    withinInterval(i1.start, i2, includeStart, includeEnd) ||
    withinInterval(i1.end, i2, includeStart, includeEnd) ||
    withinInterval(i2.start, i1, includeStart, includeEnd) ||
    withinInterval(i2.end, i1, includeStart, includeEnd)
  );
}

export function intervalToString(interval: Interval) {
  return `(${format(interval.start, "dd HH:mm")} - ${format(
    interval.end,
    "dd HH:mm"
  )})`;
}

export function intervalsSubstract(
  intervalFrom: Interval,
  intervalWhat: Interval
): Interval[] {
  const left: Interval = {
    start: Math.min(intervalFrom.start.valueOf(), intervalWhat.start.valueOf()),
    end: Math.max(intervalFrom.start.valueOf(), intervalWhat.start.valueOf())
  };
  const right: Interval = {
    start: Math.min(intervalFrom.end.valueOf(), intervalWhat.end.valueOf()),
    end: Math.max(intervalFrom.end.valueOf(), intervalWhat.end.valueOf())
  };

  const result = [left, right].filter(
    interval => interval.start < interval.end
  );
  // console.log(
  //   intervalToString(intervalFrom),
  //   "-",
  //   intervalToString(intervalWhat),
  //   "\n=",
  //   `[${[left, right].map(intervalToString).join(", ")}]`
  // );
  return result;
}
