export enum Duration {
  ms,
  sec,
  min,
  hour,
  day
}

export const Durations: Record<Duration, number> = {
  [Duration.ms]: 1,
  [Duration.sec]: 1000,
  [Duration.min]: 60 * 1000,
  [Duration.hour]: 60 * 60 * 1000,
  [Duration.day]: 24 * 60 * 60 * 1000
};

type PostProcessor = (val: number) => number;

export function convertCustom(
  value: number,
  fromCoeff: number,
  toCoeff: number,
  postProcessor = Math.floor
): number {
  return postProcessor((value * fromCoeff) / toCoeff);
}

export function convertCustomMs(
  value: number,
  toCoeff: number,
  postProcessor = Math.floor
): number {
  return postProcessor(value / toCoeff);
}

export function convert(
  value: number,
  from: Duration,
  to: Duration,
  postProcessor?: PostProcessor
): number {
  return convertCustom(value, Durations[from], Durations[to], postProcessor);
}

export function convertMs(
  value: number,
  to: Duration,
  postProcessor?: PostProcessor
): number {
  return convert(value, Duration.ms, to, postProcessor);
}
