import './MonthLabel.scss';

import { format } from 'date-fns/esm';
import React, { useMemo } from 'react';

import { ViewIntervalInfo } from '../../Calendar.containers';

const MonthLabel = () => {
  const { currentDay } = ViewIntervalInfo.useContainer();
  const currentDate = format(new Date(currentDay), "MMMM, do");
  return useMemo(() => <div styleName="monthLabel">{currentDate}</div>, [
    currentDate
  ]);
};

export default MonthLabel;
