import { addDays, addMilliseconds, differenceInDays, getDay, startOfDay } from 'date-fns';
import { useMemo, useState } from 'react';
import { createContainer } from 'unstated-next';

import { intervalsSubstract } from '../../assembly/interval';
import { shell } from '../../assembly/utility';
import { fetchAppointments } from '../../fetchers/fetchAppointments';
import { AppointmentsInfo, AppointmentUnassigned } from '../../stores/appointments';
import { GridInfo, RestrictedZonesInfo, RowsInfo, ViewIntervalInfo, Weekday } from './Calendar.containers';

function useAutoFetcherApps() {
  const { viewIntervalMax } = ViewIntervalInfo.useContainer();
  const {
    oneDayDuration,
    gridStepDuration,
    getStartOfWorkDay
  } = GridInfo.useContainer();
  const { rowsCount } = RowsInfo.useContainer();
  const { addAppointments } = AppointmentsInfo.useContainer();
  const { restrictedZones } = RestrictedZonesInfo.useContainer();

  const [nextPusingApps, setNextPusingApps] = useState<AppointmentUnassigned[]>(
    []
  );
  if (nextPusingApps.length) {
    console.log(`add ${nextPusingApps.length} apps`);
    addAppointments(nextPusingApps);
    setNextPusingApps([]);
  }

  useMemo(() => {
    const viewIntervalMaxToDays: Interval = {
      start: getStartOfWorkDay(viewIntervalMax.start),
      end: addDays(getStartOfWorkDay(viewIntervalMax.end), 1)
    };
    const daysCount = differenceInDays(
      startOfDay(viewIntervalMaxToDays.end),
      startOfDay(viewIntervalMaxToDays.start)
    );

    if (!daysCount) return;

    const intervals: Interval[] = shell(daysCount).map((_, i) => {
      const start = addDays(viewIntervalMaxToDays.start, i);
      const end = addMilliseconds(start, oneDayDuration);
      return { start, end };
    });
    // console.log(intervals.map(intervalToString));
    const intervalsRestricted = intervals
      .map(interval => {
        const weekday: Weekday = getDay(interval.start);
        const zones: Interval[] = restrictedZones[weekday].map(zone => {
          const dayOffset = differenceInDays(
            startOfDay(interval.start),
            startOfDay(zone.start)
          );
          return {
            start: addDays(zone.start, dayOffset),
            end: addDays(zone.end, dayOffset)
          };
        });
        const intervalsSplitted = zones.reduce(
          (acc: Interval[], val: Interval) =>
            acc.reduce(
              (acc2: Interval[], val2) => [
                ...acc2,
                ...intervalsSubstract(val2, val)
              ],
              []
            ),
          [interval]
        );
        // console.log(
        //   intervalToString(interval),
        //   intervalsSplitted.map(interval => intervalToString(interval))
        // );
        return intervalsSplitted;
      })
      .reduce((acc, val) => [...acc, ...val]);

    // console.log(intervals.map(interval => intervalToString(interval)));
    // console.log(
    //   intervalsRestricted.map(interval => intervalToString(interval))
    // );

    fetchAppointments(
      intervalsRestricted,
      gridStepDuration,
      70,
      rowsCount
    ).then(apps => (apps.length ? setNextPusingApps(apps) : undefined));
  }, [
    gridStepDuration,
    oneDayDuration,
    rowsCount,
    viewIntervalMax.start,
    viewIntervalMax.end,
    setNextPusingApps,
    getStartOfWorkDay,
    restrictedZones
  ]);
}

export const AutoFetcherApps = createContainer(useAutoFetcherApps);
