import './Calendar.scss';

import React, { useEffect, useMemo } from 'react';
import { useSwipeable } from 'react-swipeable';
import { useKeyPress } from 'use-hooks';

import { AppointmentsInfo } from '../../stores/appointments';
import { UIInfo } from '../../stores/ui';
import { UsersInfo } from '../../stores/users';
import { AutoFetcherApps } from './AutoFetcherApps';
import {
  AppointmentSettingsInfo,
  ColumnsInfo,
  DraggedInfo,
  DraggingAutoScrollInfo,
  DragSettingsInfo,
  GridInfo,
  GridSettingsInfo,
  LeftColumnInfo,
  RestrictedZonesInfo,
  RowBlocksInfo,
  RowsInfo,
  ScrollInfo,
  SettingsInfo,
  TimeLineInfo,
  ViewIntervalInfo,
} from './Calendar.containers';
import LeftBlock from './LeftBlock';
import MainBlock from './MainBlock';
import RightPanel from './RightPanel';
import TopBlock from './TopBlock';

const Calendar = () => {
  const { isScrolling, scrollLeft, scrollRight } = ScrollInfo.useContainer();
  const { setPageLoaded } = UIInfo.useContainer();

  const arrowRightPressed = useKeyPress("ArrowRight", true);
  const arrowLeftPressed = useKeyPress("ArrowLeft", true);

  const handlers = useSwipeable({
    onSwipedLeft: () => !isScrolling && scrollRight(),
    onSwipedRight: () => !isScrolling && scrollLeft()
  });

  if (!isScrolling)
    if (arrowRightPressed) scrollRight();
    else if (arrowLeftPressed) scrollLeft();

  useEffect(() => {
    (async () => {
      // const daysCount = 20;
      // const daysLeftCount = Math.floor(daysCount / 2);
      // const maxLeftTime = addDays(initialZeroAxis, -daysLeftCount);
      // const intervals: Interval[] = shell(daysCount).map((_, i) => {
      //   const start = addDays(maxLeftTime, i);
      //   const end = addMilliseconds(start, oneDayDuration);
      //   return { start, end };
      // });

      // console.log(intervals);

      // addAppointments(
      //   await fetchAppointments(intervals, gridStepDuration, 70, rowsCount)
      // );

      setPageLoaded(true);
    })();

    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return useMemo(
    () => (
      <ViewIntervalInfo.Provider>
        <AutoFetcherApps.Provider>
          <div styleName="calendar" {...handlers}>
            <div styleName="top">
              <TopBlock />
            </div>
            <RowBlocksInfo.Provider>
              <div styleName="bottom">
                <LeftBlock />
                <TimeLineInfo.Provider>
                  <DraggingAutoScrollInfo.Provider>
                    <MainBlock minColumnWidth={200} />
                  </DraggingAutoScrollInfo.Provider>
                </TimeLineInfo.Provider>
              </div>
            </RowBlocksInfo.Provider>
          </div>
        </AutoFetcherApps.Provider>
      </ViewIntervalInfo.Provider>
    ),
    [handlers]
  );
};

const WrapperProviders = () => {
  return (
    <RestrictedZonesInfo.Provider>
      <UsersInfo.Provider>
        <DraggedInfo.Provider>
          <GridInfo.Provider>
            <RowsInfo.Provider>
              <LeftColumnInfo.Provider>
                <AppointmentsInfo.Provider initialState={{ blockSize: 10 }}>
                  <ScrollInfo.Provider>
                    <Calendar />
                  </ScrollInfo.Provider>
                </AppointmentsInfo.Provider>
              </LeftColumnInfo.Provider>
            </RowsInfo.Provider>
          </GridInfo.Provider>
        </DraggedInfo.Provider>
      </UsersInfo.Provider>
    </RestrictedZonesInfo.Provider>
  );
};

const WrapperSettings = () => (
  <DragSettingsInfo.Provider>
    <AppointmentSettingsInfo.Provider>
      <ColumnsInfo.Provider>
        <GridSettingsInfo.Provider>
          <SettingsInfo.Provider>
            <WrapperProviders />
            <RightPanel />
          </SettingsInfo.Provider>
        </GridSettingsInfo.Provider>
      </ColumnsInfo.Provider>
    </AppointmentSettingsInfo.Provider>
  </DragSettingsInfo.Provider>
);

export default WrapperSettings;
