import { differenceInMilliseconds } from 'date-fns';
import React, { useMemo, useRef } from 'react';

import { convert, convertMs, Duration } from '../../../../../../assembly/timeConverts';
import { Appointment, AppointmentsBlock } from '../../../../../../stores/appointments';
import { RowBlock } from '../../../../Calendar.containers';
import AppointmentWrapper from '../AppointmentWrapper';

export interface AppointmentBlockProps {
  appsBlock: AppointmentsBlock;
  rowHeight: number;
  columnWidth: number;
  zeroAxis: Date | number;
  gridStepDuration: Date | number;
  dayDuration: number;
  dayStartTime: number;
  rowBlocks: RowBlock[];
}

function generateApp({
  app,
  rowHeight,
  columnWidth,
  zeroAxis,
  gridStepDuration,
  dayDuration,
  rowBlocks
}: {
  app: Appointment;
  rowHeight: number;
  columnWidth: number;
  zeroAxis: number | Date;
  gridStepDuration: number | Date;
  dayDuration: number;
  dayStartTime: number;
  rowBlocks: RowBlock[];
}): JSX.Element {
  const appDuration = app.interval.end - app.interval.start;
  // console.log(appDuration);

  const height = rowHeight;
  const width = columnWidth * (appDuration / gridStepDuration.valueOf());
  const timeOffset = differenceInMilliseconds(app.interval.start, zeroAxis);
  const daysOffset = convertMs(timeOffset, Duration.day);
  const dayStartOffset =
    timeOffset - convert(daysOffset, Duration.day, Duration.ms);
  const dayColumns = dayDuration / gridStepDuration.valueOf();

  const rowBlocksPast = Math.max(
    rowBlocks.filter(block => block.start <= app.rowIndex).length,
    1
  );

  const daysOffsetColumns = daysOffset * dayColumns;
  const dayStartOffsetColumns = dayStartOffset / gridStepDuration.valueOf();
  const x = (daysOffsetColumns + dayStartOffsetColumns) * columnWidth;
  const y = (app.rowIndex + rowBlocksPast) * rowHeight;

  // console.log(`${format(timeOffset, "dd HH:mm")} + ${daysOffset} -> ${x}`);

  return (
    <AppointmentWrapper
      key={app.id}
      height={height}
      width={width}
      left={x}
      top={y}
      app={app}
    />
  );
}

// const AppointmentBlockInternal =

const AppointmentBlock = React.memo(
  ({
    appsBlock,
    rowHeight,
    gridStepDuration,
    columnWidth,
    zeroAxis,
    dayDuration,
    dayStartTime,
    rowBlocks
  }: AppointmentBlockProps) => {
    //  console.log("app block updated");
    const cachedApps = useRef<Record<string, JSX.Element>>({});

    const cachedByEnvironment = useMemo(
      () =>
        appsBlock.elements.reduce(
          (acc: Record<string, JSX.Element>, app) => ({
            ...acc,
            [app.id]: generateApp({
              app,
              columnWidth,
              gridStepDuration,
              rowHeight,
              zeroAxis,
              dayDuration,
              dayStartTime,
              rowBlocks
            })
          }),
          {}
        ),
      [
        rowHeight,
        gridStepDuration,
        columnWidth,
        zeroAxis,
        appsBlock.elements,
        dayDuration,
        dayStartTime,
        rowBlocks
      ]
    );

    const appElements = appsBlock.elements.reduce(
      (acc: Record<string, JSX.Element>, app) => ({
        ...acc,
        [app.id]:
          cachedByEnvironment[app.id] ||
          cachedApps.current[app.id] ||
          generateApp({
            app,
            columnWidth,
            gridStepDuration,
            rowHeight,
            zeroAxis,
            dayDuration,
            dayStartTime,
            rowBlocks
          })
      }),
      {}
    );

    // tslint:disable-next-line:no-object-mutation
    cachedApps.current = appElements;

    return <>{Object.values(appElements)}</>;
  }
);

export default AppointmentBlock;
