import './RestrictedZone.scss';

import React from 'react';

export interface RestrictedZoneProps {
  width: number;
  height: number;
  x: number;
  y: number;
}

const RestrictedZone = ({ width, height, x, y }: RestrictedZoneProps) => {
  return (
    <div
      styleName="restrictedZone"
      style={{ width, height, left: x, top: y }}
    />
  );
};

export default React.memo(RestrictedZone);
