import { addMinutes } from 'date-fns';
import { useState } from 'react';
import { createContainer } from 'unstated-next';

function useMock() {
  const [stepDuration, setStepDuration] = useState(addMinutes(0, 40));

  return {
    setStepDuration,
    stepDuration
  };
}

export const MockInfo = createContainer(useMock);
