import { createContainer } from 'unstated-next';

// tslint:disable
export interface LazyTask<T> {
  callback: () => T;
  priority: number;
  type: string;
}

export interface LazyTaskPromised<T> extends LazyTask<T> {
  resolve: (result: T) => void;
}

export type TimeElapsedMap = Record<string, number[]>;

type LazyTaskStack = Record<number, Array<LazyTaskPromised<any>>>;

export function useLazyTask() {
  const lazyTasks: LazyTaskStack = {};
  const postEffectTimeMap: TimeElapsedMap = {};
  // let lastFrameEndTime = Date.now();

  const loop = () => {
    // const currentFrameTime = Date.now();
    // const preFrameDuration = currentFrameTime - lastFrameEndTime;
    // // console.log("preFrameDuration:", preFrameDuration);

    // const totalTasksDuration = executeTasks(
    //   lazyTasks,
    //   postEffectTimeMap,
    //   20,
    //   preFrameDuration
    // );

    // lastFrameEndTime = Date.now() + totalTasksDuration;
    executeTasks(lazyTasks, postEffectTimeMap, 16, 0);

    requestAnimationFrame(loop);
  };

  loop();

  const addLazyTask = <T>(task: LazyTask<T>): Promise<T> => {
    return new Promise(resolve => {
      const taskPromised: LazyTaskPromised<T> = {
        ...task,
        resolve: value => resolve(value)
      };

      lazyTasks[task.priority] = [
        ...(lazyTasks[task.priority] || []),
        taskPromised
      ];
    });
  };

  const addLazyTasks = <T>(
    tasks: Array<Omit<LazyTask<T>, "priority" | "type">>,
    priority: number,
    type: string
  ): Promise<T[]> => {
    const tasksPromised: LazyTaskPromised<T>[] = [];
    const promises = tasks.map(task => {
      const promise = new Promise<T>(resolve => {
        const taskPromised: LazyTaskPromised<T> = {
          ...task,
          priority,
          type,
          resolve: value => resolve(value)
        };

        tasksPromised.push(taskPromised);
      });

      return promise;
    });

    lazyTasks[priority] = [...(lazyTasks[priority] || []), ...tasksPromised];

    return Promise.all(promises);
  };

  const registerPostEffectDuration = (
    type: string,
    duration: number,
    tasksExecuted: number
  ) => {
    postEffectTimeMap[type] = (postEffectTimeMap[type] || [])
      .concat(duration / tasksExecuted)
      .slice(-10);
    // console.log(postEffectTimeMap[type]);
  };
  return { addLazyTask, addLazyTasks, registerPostEffectDuration };
}

function executeTasks(
  tasks: LazyTaskStack,
  postEffectTimeMap: TimeElapsedMap,
  maxFrameDuration: number,
  preFrameDuration: number
) {
  const startTime = Date.now();

  const layers = Object.entries(tasks).sort(([key1], [key2]) =>
    parseInt(key1, 10) > parseInt(key2, 10) ? -1 : 1
  );
  let nowTime = Date.now();
  let postEffectDuration = 0;
  while (
    nowTime - startTime + postEffectDuration + preFrameDuration <
      maxFrameDuration &&
    layers.length
  ) {
    const task = layers[0][1].shift();

    if (task) {
      const result = task.callback();
      task.resolve(result);

      const lastTimes = postEffectTimeMap[task.type] || [0];
      postEffectDuration +=
        lastTimes.reduce((acc, val) => acc + val) / lastTimes.length;
    } else layers.shift();

    nowTime = Date.now();
  }

  // console.log("posteffect:", postEffectDuration);

  return Date.now() - startTime + postEffectDuration;
}

export const LazyTaskContainer = createContainer(useLazyTask);
