import './AppointmentWrapper.scss';

import classnames from 'classnames';
import React, { useCallback, useLayoutEffect, useMemo, useRef, useState } from 'react';
import { Portal } from 'react-portal';

import { Appointment } from '../../../../../../stores/appointments';
import { DraggedInfo, DragSettingsInfo } from '../../../../Calendar.containers';
import AppointmentCell from './AppointmentCell';

interface Props {
  width: number;
  height: number;
  left: number;
  top: number;
  app: Appointment;
}

const AppointmentWrapper = ({ width, height, left, top, app }: Props) => {
  const {
    stateOnDragging: {
      state: [onDragging]
    }
  } = DragSettingsInfo.useContainer();

  const [mouseDownCoords, setMouseDownCoords] = useState<{
    x: number;
    y: number;
  } | null>(null);
  const [offset, setOffset] = useState<{ dx: number; dy: number }>({
    dx: 0,
    dy: 0
  });
  const [coords, setCoords] = useState({ x: 0, y: 0 });
  const { setDragOffsetToAppCell } = DraggedInfo.useContainer();

  const appDraggablePortal = useMemo(
    () => document.getElementById("appDraggablePortal") as HTMLDivElement,
    []
  );

  const ref = useRef<any>();

  const isDragging = !!mouseDownCoords;

  const onMouseMove = useCallback(
    (event: MouseEvent) => {
      if (!mouseDownCoords) return;

      const { clientX: x, clientY: y } = event;
      const dx = x - mouseDownCoords.x;
      const dy = y - mouseDownCoords.y;

      setOffset({ dx, dy });
    },
    [mouseDownCoords]
  );

  const onMouseDown = useCallback(
    (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
      if (!onDragging) return;

      const appElement = ref.current as HTMLDivElement;
      const { left: x, top: y } = appElement.getBoundingClientRect();

      const dx = event.pageX - x;
      const dy = event.pageY - y;

      setCoords({ x, y });
      setDragOffsetToAppCell({ dx, dy });
      setMouseDownCoords({ x: event.clientX, y: event.clientY });
    },
    [setDragOffsetToAppCell, onDragging]
  );

  const onMouseUp = useCallback(() => {
    setMouseDownCoords(null);
    setOffset({ dx: 0, dy: 0 });
  }, []);

  useLayoutEffect(() => {
    if (mouseDownCoords) {
      window.addEventListener("mousemove", onMouseMove);
      addEventListener("mouseup", () => {
        removeEventListener("mousemove", onMouseMove);
        onMouseUp();
      });
    }

    return () => removeEventListener("mousemove", onMouseMove);
  }, [mouseDownCoords, onMouseMove, onMouseUp]);

  const appCell = useMemo(
    () => <AppointmentCell app={app} width={width} height={height} />,
    [app, width, height]
  );

  const staticAppWrapper = useMemo(
    () => (
      <div
        styleName={classnames(
          "appointmentWrapper",
          isDragging ? "dragOrigin" : ""
        )}
        style={{
          height,
          transform: `translate(${left}px, ${top}px)`,
          width
        }}
        onMouseDownCapture={onMouseDown}
        onMouseUpCapture={onMouseUp}
        ref={ref}
      >
        {appCell}
      </div>
    ),
    [left, top, height, width, onMouseDown, onMouseUp, appCell, isDragging]
  );

  const draggingAppWrapper = useMemo(
    () =>
      isDragging ? (
        <Portal node={appDraggablePortal}>
          <div
            styleName={classnames("appointmentWrapper", "dragging")}
            style={{
              height,
              transform: `translate(${coords.x + offset.dx}px, ${coords.y +
                offset.dy}px)`,
              width
            }}
            onMouseDownCapture={onMouseDown}
            onMouseUpCapture={onMouseUp}
          >
            {appCell}
          </div>
        </Portal>
      ) : null,
    [
      height,
      width,
      onMouseDown,
      onMouseUp,
      appCell,
      coords,
      isDragging,
      offset,
      appDraggablePortal
    ]
  );

  return useMemo(
    () => (
      <>
        {staticAppWrapper}
        {draggingAppWrapper}
      </>
    ),
    [staticAppWrapper, draggingAppWrapper]
  );
};

export default React.memo(AppointmentWrapper);
