import { useState } from 'react';
import { createContainer } from 'unstated-next';
import { v4 } from 'uuid';

export interface User {
  id: string;
  name: string;
  secondName: string;
}

export function createUser(
  args: Partial<User> & Omit<User, "id" | "secondName">
): User {
  return {
    ...args,
    id: args.id || v4(),
    secondName: args.secondName || ""
  };
}

function useUsers() {
  const [users, setUsers] = useState<Record<string, User>>({});

  const addUser = (user: User) => setUsers({ ...users, [user.id]: user });
  const addUsers = (newUsers: User[]) =>
    setUsers({
      ...users,
      ...newUsers.reduce(
        (acc: Record<string, User>, user) => ({ ...acc, [user.id]: user }),
        {}
      )
    });

  return { users, addUser, addUsers };
}

export const UsersInfo = createContainer(useUsers);
