import './RestrictedZones.scss';

import { addDays, differenceInDays, differenceInMilliseconds, eachDayOfInterval, getDay } from 'date-fns';
import React, { ReactNode, useCallback, useMemo } from 'react';

import { convert, convertMs, Duration } from '../../../../../assembly/timeConverts';
import {
  ColumnsInfo,
  GridInfo,
  RestrictedZonesInfo,
  RowBlocksInfo,
  RowsInfo,
  ViewIntervalInfo,
  Weekday,
} from '../../../Calendar.containers';
import RestrictedZone from './RestrictedZone/RestrictedZone';

const RestrictedZones = () => {
  const { restrictedZones } = RestrictedZonesInfo.useContainer();
  const { rowBlocks } = RowBlocksInfo.useContainer();
  const { viewIntervalMax } = ViewIntervalInfo.useContainer();
  const {
    zeroAxis,
    gridStepDuration,
    oneDayDuration,
    getStartOfWorkDay
  } = GridInfo.useContainer();
  const { columnWidth } = ColumnsInfo.useContainer();
  const { rowHeight, rowsCount } = RowsInfo.useContainer();

  const generateZone = useCallback(
    (interval: Interval) => {
      const duration = differenceInMilliseconds(
        interval.end.valueOf(),
        interval.start
      );
      const height = rowHeight * (rowsCount + 1 + rowBlocks.length);
      const width = columnWidth * (duration / gridStepDuration.valueOf());
      const timeOffset = differenceInMilliseconds(interval.start, zeroAxis);
      const daysOffset = convertMs(timeOffset, Duration.day);
      const dayStartOffset =
        timeOffset - convert(daysOffset, Duration.day, Duration.ms);
      const dayColumns = oneDayDuration / gridStepDuration.valueOf();

      const daysOffsetColumns = daysOffset * dayColumns;
      const dayStartOffsetColumns = dayStartOffset / gridStepDuration.valueOf();
      const x = (daysOffsetColumns + dayStartOffsetColumns) * columnWidth;
      const y = 0;

      // console.log(
      //   `zone spawned at ${x} for ${format(interval.start, "dd HH:mm")}`
      // );

      return (
        <RestrictedZone
          {...{ x, y, width, height }}
          key={interval.start.valueOf()}
        />
      );
    },
    [
      columnWidth,
      rowsCount,
      gridStepDuration,
      oneDayDuration,
      rowHeight,
      zeroAxis
    ]
  );

  const zones = useMemo(() => {
    return eachDayOfInterval(viewIntervalMax).reduce(
      (acc: JSX.Element[], day: Date) => {
        const dayStart = getStartOfWorkDay(day);
        const weekday: Weekday = getDay(dayStart);
        const restrictedIntervals = restrictedZones[weekday];

        if (!restrictedIntervals.length) return acc;

        const dayOffset = differenceInDays(
          dayStart,
          getStartOfWorkDay(restrictedIntervals[0].start)
        );

        // const dayOffsetDuration = dayOffset * oneDayDuration;
        // console.log({
        //   millisOffset: differenceInMilliseconds(dayStart, zeroAxis),
        //   dayOffset,
        //   dayOffsetDuration,
        //   weekday: format(setDay(Date.now(), weekday), "EE"),
        //   restrictedIntervals: restrictedIntervals.length,
        //   intervalStart: format(restrictedIntervals[0].start, "dd HH:mm"),
        //   intervalStartNormalized: format(
        //     addDays(restrictedIntervals[0].start, dayOffset),
        //     "dd HH:mm"
        //   ),
        //   day: dayStart,
        //   zeroAxis
        // });

        return [
          ...acc,
          ...restrictedIntervals.map(interval =>
            generateZone({
              start: addDays(interval.start, dayOffset),
              end: addDays(interval.end, dayOffset)
            })
          )
        ];
      },
      []
    );
  }, [
    generateZone,
    restrictedZones,
    viewIntervalMax,
    zeroAxis,
    oneDayDuration,
    getStartOfWorkDay
  ]);

  return useMemo(() => {
    // console.log(zones.length);
    return <div styleName="restrictedZone">{zones}</div>;
  }, [zones]);
};

export default RestrictedZones;
