import './AppointmentCell.scss';

import classnames from 'classnames';
import { format } from 'date-fns';
import React from 'react';

import { Appointment } from '../../../../../../../stores/appointments';
import CenteredHorizontally from '../../../../../../../style-components/CenteredHorizontally';
import CenteredVertically from '../../../../../../../style-components/CenteredVertically';
import { AppointmentSettingsInfo } from '../../../../../Calendar.containers';

export interface AppointmentCellProps {
  app: Appointment;
  width: number;
  height: number;
}

const AppointmentCell = (props: AppointmentCellProps) => {
  const {
    stateVisibilityAvatar: {
      state: [visibilityAvatar]
    },
    stateVisibilityVisits: {
      state: [visibilityVisits]
    }
  } = AppointmentSettingsInfo.useContainer();

  return (
    <div
      styleName={classnames(
        "appointmentCell",
        visibilityAvatar ? "" : "noAvatar",
        visibilityVisits ? "" : "noVisits"
      )}
    >
      <div styleName="leftBlock">
        <span styleName="marker" />
        <CenteredVertically>
          <span styleName="avatar" />
        </CenteredVertically>
        <CenteredVertically>
          <span styleName="info">{`${props.app.id.split("-")[0]}, ${format(
            props.app.interval.start,
            "dd H:mm"
          )}, ${props.app.rowIndex}`}</span>
        </CenteredVertically>
      </div>
      <div styleName="rightBlock">
        <CenteredVertically>
          <span styleName="points">
            <CenteredHorizontally>
              <CenteredVertically>
                <span>15</span>
              </CenteredVertically>
            </CenteredHorizontally>
          </span>
        </CenteredVertically>
      </div>
    </div>
  );
};

export default AppointmentCell;
