import './RowBlocks.scss';

import React from 'react';

import { RowBlocksInfo, RowsInfo } from '../../../Calendar.containers';

const RowBlocks = () => {
  const { rowBlocks } = RowBlocksInfo.useContainer();
  const { rowHeight } = RowsInfo.useContainer();

  return (
    <div styleName="rowBlocks">
      {rowBlocks.map((block, i) => {
        const y = (block.start + i + 1) * rowHeight;
        return (
          <div
            styleName="block"
            key={block.name}
            style={{ top: y, height: rowHeight }}
          >
            <span styleName="label">{block.name}</span>
          </div>
        );
      })}
    </div>
  );
};

export default RowBlocks;
