type Animation = (delta: number) => void;
const animations: Map<Animation, Animation> = new Map();

let lastTime = Date.now();
const loop = () => {
  const nowTime = Date.now();
  const delta = nowTime - lastTime;
  lastTime = nowTime;

  animations.forEach(anim => anim(delta));
  requestAnimationFrame(loop);
};

loop();

export const addAnimation = (animation: Animation): void => {
  animations.set(animation, animation);
};

export const removeAnimation = (animation: Animation): void => {
  animations.delete(animation);
};
