import './RightPanel.scss';

import classnames from 'classnames';
import React, { useState } from 'react';
import { useKeyPress, usePrevious } from 'use-hooks';

import SettingsBlock from './SettingsBlock';

const RightPanel = () => {
  const [opened, setOpened] = useState(false);
  const backquote = useKeyPress("`", true);
  const ye = useKeyPress("ё", true);

  const openedPrevious = usePrevious(opened);

  if ((backquote || ye) && openedPrevious === opened) setOpened(!opened);

  return (
    <div styleName={classnames("rightPanel", opened ? "opened" : "")}>
      <SettingsBlock />
    </div>
  );
};

export default RightPanel;
